import "./Bpm.css";
import { useState, useEffect } from "react";
import Random from "./Random";
import Clavier from "./Clavier";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

export default function Bpm(props) {
  const [speed, setSpeed] = useState(0);
  const [pause, setPause] = useState(true);

  const [accord, setAccord] = useState();

  function togglePP() {
    if (pause) {
      setSpeed(1);
      setPause(false);
    } else if (!pause) {
      setSpeed(0);
      setPause(true);
    }
  }

  function less() {
    if (speed > 0) {
      setSpeed(speed - 1);
    }
    if (speed === 1) {
      setPause(true);
    }
  }

  function more() {
    if (speed <= 4) {
      setSpeed(speed + 1);
    }
    if (speed === 0) {
      setPause(false);
    }
  }

  useEffect(() => {
    if (speed >= 1) {
      let interval = setInterval(() => {
        let rand = Math.floor(Math.random() * 4);

        console.log(rand);
        setAccord(() => rand);
      }, speed * 1000);

      return () => clearInterval(interval);
    }
  }, [speed]);

  return (
    <>
      <div className="bpm">
        <p>
          Vitesse :
          <span className="no-wrap">
            <button className="less" onClick={() => less()}>
              -
            </button>
            {speed}
            <button className="more" onClick={() => more()}>
              +
            </button>
          </span>
        </p>
        <button onClick={togglePP} className="toggle-btn">
          {pause ? (
            <i className="fas fa-play"></i>
          ) : (
            <i className="fas fa-pause"></i>
          )}
        </button>
      </div>
      <Router>
        <Switch>
          <Route path="/">
            <Random active={props.active} accord={accord} pause={pause} />

            <Clavier active={props.active} accord={accord} pause={pause} />
          </Route>
        </Switch>
      </Router>
    </>
  );
}
