import "./Random.css";

export default function Random(props) {
  let accord = props.accord;

  switch (accord) {
    case 0:
      accord = "majeur";

      break;

    case 1:
      accord = "mineur";

      break;

    case 2:
      accord = "augmenté";

      break;

    case 3:
      accord = "diminué";

      break;

    default:
      accord = "";
      break;
  }

  console.log(props.active);

  return (
    <>
      {!props.pause ? (
        <div className="random">
          {props.active} {accord}
        </div>
      ) : (
        <div className="random">Pause</div>
      )}
    </>
  );
}
