import "./App.css";
import Bpm from "./Components/Bpm";
import { useState, useEffect } from "react";

export default function App() {
  const [note, setNote] = useState([
    { note: "do", key: 0 },
    { note: "do#", key: 1 },
    { note: "re", key: 2 },
    { note: "re#", key: 3 },
    { note: "mi", key: 4 },
    { note: "fa", key: 5 },
    { note: "fa#", key: 6 },
    { note: "sol", key: 7 },
    { note: "sol#", key: 8 },
    { note: "la", key: 9 },
    { note: "la#", key: 10 },
    { note: "si", key: 11 },
  ]);

  useEffect(() => {}, []);

  const [noteActive, setNoteActive] = useState("do");

  const noteAct = (e) => {
    setNoteActive(e.target.value);
  };

  return (
    <>
      <h1>Les accords au piano</h1>
      <main>
        <ul className="select">
          <button className="rand">Aléatoire</button>
          {note.map((el) => {
            return (
              <>
                <button
                  className={el.note == noteActive && "act"}
                  key={el.key}
                  value={el.note}
                  onClick={(e) => noteAct(e)}
                >
                  {el.note}
                </button>
              </>
            );
          })}
        </ul>

        <Bpm active={noteActive} />
      </main>
    </>
  );
}
